import { Component, Input } from '@angular/core';
import {
  PaymentTypeConfig,
  RemittanceDetails,
} from '@backbase/payment-orders-ang';
import {
  Currency,
  ForexDetails,
  ChargeBearer,
} from '@backbase/payment-orders-ang';

@Component({
  selector: 'bb-payment-remittance-details',
  templateUrl: './remittance-details.component.html',
})
export class RemittanceDetailsComponent {
  /**
   * Details of Remittance other than initiator and counterparty
   */
  @Input() data!: RemittanceDetails;

  /**
   * The payment config item
   */
  @Input() paymentConfig?: PaymentTypeConfig;

  /**
   * The currency exchange details of the payment
   */
  @Input() forexDetails?: ForexDetails;

  /**
   * The fee details for the payment.
   */
  @Input() transferFee?: Currency;

  /**
   * The charge bearer type of teh payment
   */
  @Input() chargeBearer?: ChargeBearer;
}
